footer {
    background-color: #6DAAC0;
    text-align: center;
    padding: 10px;
}

footer h3 {
    font-family: 'Open Sans', cursive;
}

footer img {
    background-color: white;
    border-radius: 8px;
    padding: 4px;
    width: 20px;
    height: 20px;
}

.footer_img {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.footer_img img {
    background-color: transparent;
}

.footer_img p {
    font-family: 'Times New Roman', Times, serif;
    margin-top: 6px;
}

.contact_list {
    list-style-type: none;
    padding: 0;
    display: flex;
    justify-content: center;
}

.contact_list a {
    margin-bottom: 4px;
    padding: 15px;
    text-decoration: none;
}